*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 93.75%;
}

body {
  font-family: "Alata", sans-serif;
  font-size: calc(1rem + 0.75vw);
}

a {
  color: white;
  text-decoration: none;
}

a,
p {
  font-size: 0.875rem;
}
@media screen and (min-width: 999px) {
  a,
p {
    font-size: 1rem;
  }
}

h1,
h2,
h3,
h4 {
  font-family: "Alata", sans-serif;
  margin-top: 0px;
}

.container {
  max-width: 1116px;
  margin: 0 auto;
}

li {
  list-style-type: none;
}

.header {
  background: url("../../images/mobile/image-hero.jpg") no-repeat center center/cover;
  height: 100vh;
  padding: 48px 26px;
}
@media screen and (min-width: 699px) {
  .header {
    background: url("../../images/desktop/image-hero.jpg") no-repeat center center/cover;
    padding-top: 60px;
    padding-bottom: 143px;
  }
}
.header__hero nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.header__hero nav ul {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  background: #000;
  height: 100vh;
  z-index: 2;
  padding: 2em;
  transform: translateX(100%);
  transition: transform 0.3s;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul {
    width: unset;
    position: unset;
    transform: translateX(0);
    background: none;
    height: auto;
    padding-top: 1em;
  }
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links {
    display: flex;
  }
}
.header__hero nav ul .nav-links a {
  padding: 2px;
  position: relative;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links a {
    margin-right: 20px;
  }
}
.header__hero nav ul .nav-links a::before {
  content: "";
  display: block;
  position: absolute;
  height: 2px;
  width: 50%;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: -10px;
  background: #fff;
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__hero nav ul .nav-links a:hover::before {
  opacity: 1;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links .last {
    margin-right: 0px;
  }
}
.header__hero nav ul .logo {
  margin-bottom: 100px;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .logo {
    display: none;
  }
}
.header__hero nav ul .nav-links a {
  font-family: "Josefin Sans", sans-serif;
  font-size: 36px;
  text-transform: uppercase;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links a {
    font-family: "Alata", sans-serif;
    font-size: 0.875rem;
    text-transform: initial;
  }
}
.header__hero nav ul .nav-links a li {
  margin-bottom: 30px;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links a li {
    margin-bottom: 0px;
  }
}
.header__hero-content {
  margin-top: 200px;
  border: 2px solid #fff;
  text-transform: uppercase;
  padding: 32px 36px;
}
@media screen and (min-width: 699px) {
  .header__hero-content {
    max-width: 450px;
    margin-top: 126px;
  }
}
@media screen and (min-width: 999px) {
  .header__hero-content {
    max-width: 650px;
  }
}
.header__hero-content h1 {
  font-family: "Josefin Sans", sans-serif;
  color: #fff;
  font-weight: 300;
}
.header__hero-content h1 .down-text {
  display: block;
}
@media screen and (min-width: 999px) {
  .header__hero-content h1 .down-text {
    display: initial;
  }
}

.menu-btn,
.exit-btn {
  cursor: pointer;
}
@media screen and (min-width: 699px) {
  .menu-btn,
.exit-btn {
    display: none;
  }
}

.mobile-nav-head {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}

.about {
  margin: 121px 30px;
}
.about__info {
  position: relative;
}
.about__info-img {
  background: url("../../images/mobile/image-interactive.jpg") no-repeat center center/cover;
  width: 87.42%;
  height: calc(285px + 10vw);
  margin: 0 auto;
  margin-bottom: 62px;
}
@media screen and (min-width: 699px) {
  .about__info-img {
    max-width: 529px;
  }
}
@media screen and (min-width: 999px) {
  .about__info-img {
    background: url("../../images/desktop/image-interactive.jpg") no-repeat center center/cover;
    max-width: 729px;
    height: 504px;
    margin-left: 0;
  }
}
.about__info-text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
@media screen and (min-width: 699px) {
  .about__info-text {
    width: 60%;
  }
}
@media screen and (min-width: 999px) {
  .about__info-text {
    width: 542px;
    height: 319px;
    position: absolute;
    bottom: 0;
    right: 0;
    background: #fff;
    padding-top: 95px;
    padding-left: 96px;
    text-align: unset;
  }
}
.about__info-text h2 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 35px;
  font-size: 30px;
}
@media screen and (min-width: 999px) {
  .about__info-text h2 {
    font-size: 36px;
  }
}
.about__info-text p {
  color: dimgray;
  font-size: 18px;
  line-height: 1.3;
}
@media screen and (min-width: 999px) {
  .about__info-text p {
    font-size: 14px;
  }
}